.social {
	width: auto;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: flex-end;
	gap: .5rem;
	color: var(--gray-200);
	
	@media (min-width: 992px) {
		width: 100%;
		justify-content: space-between;
		gap: 0;
	}

	&__link {
		--_color: inherit;

		--_icon-size: 2rem;
	}
}