.header {
	color: var(--gray);
	padding-block: 3rem;
	pointer-events: none;
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	z-index: 1030;

	&-container {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		gap: 4rem;
		text-align: end;

		.btn {
			--_color: inherit;

			pointer-events: auto;
		}
	}

	&-nav {
		text-align: end;

		+ .header-nav {
			margin-top: 2rem;
		}

		+ .contacts {
			margin-top: 4rem;
		}

		.nav__list {
			display: flex;
			flex-direction: column;
			gap: .5rem;

			.btn {
				--_color: inherit;

				pointer-events: auto;
			}
		}
	}
}