// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "GillSans", sans-serif;
  --content-width: 1700px;
  --container-offset: 1.25rem;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // sizes
  --fs-base: 1.25rem;
  --fw-base: 300;
  --lh-base: normal;

  // colors
  --text-color: var(--gray-600);
  --light: #fff;
  --gray: #808080;
  --gray-200: #e6e6e6;
  --gray-300: #b3b3b3;
  --gray-400: #999;
  --gray-500: #5e5e5e;
  --gray-600: #4d4d4d;
  --gray-800: #333;

  --light-rgb: 255, 255, 255;
  --gray-rgb: 128, 128, 128;
  --gray-200-rgb: 230, 230, 230;
  --gray-300-rgb: 179, 179, 179;
  --gray-400-rgb: 153, 153, 153;
  --gray-500-rgb: 94, 94, 94;
  --gray-600-rgb: 77, 77, 77;
  --gray-800-rgb: 51, 51, 51;

  // backgrounds
  --body-bg: var(--gray-200);

  // radius
  --rounded: 1rem;

  // shadows

  // transitions
  --transition-duration: 300ms;
  --transition-timing-function: ease-in-out;

  @media (max-width: 992px) {
    --fs-base: 1rem;
  }
}
