// stylelint-disable function-disallowed-list

%offcanvas-css-vars {
  // scss-docs-start offcanvas-css-vars
  --bs-offcanvas-zindex: 1045;
  --bs-offcanvas-width: 100%;
  --bs-offcanvas-height: 100%;
  --bs-offcanvas-padding: 3rem var(--container-offset);
  --bs-offcanvas-color: var(--gray-300);
  --bs-offcanvas-bg: rgba(var(--gray-500-rgb), .5);
  --bs-offcanvas-border-width: 0;
  --bs-offcanvas-border-color: transparent;
  --bs-offcanvas-box-shadow: unset;
  --bs-offcanvas-transition: transform var(--transition-duration) var(--transition-timing-function);
  --bs-offcanvas-title-line-height: var(--lh-base);

  @media (min-width: 992px) {
    --bs-offcanvas-width: 25rem;
    --bs-offcanvas-padding: 3rem 4rem;
    --bs-offcanvas-bg: var(--gray-500);
  }
}

.offcanvas {
  @extend %offcanvas-css-vars;

  position: fixed;
  bottom: 0;
  z-index: var(--bs-offcanvas-zindex);
  display: flex;
  flex-direction: column;
  max-width: 100%;
  color: var(--bs-offcanvas-color);
  visibility: hidden;
  background-color: var(--bs-offcanvas-bg);
  background-clip: padding-box;
  outline: 0;
  transition: var(--bs-offcanvas-transition);

  @media (max-width: 992px) {
    backdrop-filter: blur(1.25rem);
  }

  &.offcanvas-start {
    top: 0;
    left: 0;
    min-width: var(--bs-offcanvas-width);
    border-right: var(--bs-offcanvas-border-width) solid var(--bs-offcanvas-border-color);
    transform: translateX(-100%);
  }

  &.offcanvas-end {
    top: 0;
    right: 0;
    width: var(--bs-offcanvas-width);
    border-left: var(--bs-offcanvas-border-width) solid var(--bs-offcanvas-border-color);
    transform: translateX(100%);
  }

  &.offcanvas-top {
    top: 0;
    right: 0;
    left: 0;
    height: var(--bs-offcanvas-height);
    max-height: 100%;
    border-bottom: var(--bs-offcanvas-border-width) solid var(--bs-offcanvas-border-color);
    transform: translateY(-100%);
  }

  &.offcanvas-bottom {
    right: 0;
    left: 0;
    height: var(--bs-offcanvas-height);
    max-height: 100%;
    border-top: var(--bs-offcanvas-border-width) solid var(--bs-offcanvas-border-color);
    transform: translateY(100%);
  }

  &.showing,
  &.show:not(.hiding) {
    transform: none;
  }

  &.showing,
  &.hiding,
  &.show {
    visibility: visible;
  }
}

.offcanvas-backdrop {
  display: block;
  background-color: transparent;
  cursor: pointer;
  position: fixed;
  inset: 0;
  z-index: 1040;
}

.offcanvas-header {
  display: flex;
  align-items: center;
  padding: var(--bs-offcanvas-padding);

  .btn-close,
  .burger {
    margin-left: auto;
  }

  .burger {
    --_color-active: inherit;
  }
}

.offcanvas-title {
  margin-bottom: 0;
  line-height: var(--bs-offcanvas-title-line-height);
}

.offcanvas-header {
  padding-bottom: 2rem;
}

.offcanvas-body {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: var(--bs-offcanvas-padding);
  padding-top: 2rem;
  overflow-y: auto;

  .contacts {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    margin-top: auto;
  }
}
